import React, {Component} from 'react';
import './App.scss';
import {COLORS} from "./colors";
import anime from 'animejs/lib/anime.es';

const Heart = (props) => (
  <div
    className="Heart"
    onClick={() => props.onClick()}>
    <span>I</span>
    <span> </span>
    <span>L</span>
    <span>O</span>
    <span>V</span>
    <span>E</span>
    <span> </span>
    <span>Y</span>
    <span>O</span>
    <span>U</span>
    <br/>
    <span>B</span>
    <span>E</span>
    <span>L</span>
    <span>L</span>
    <span>I</span>
    <span>N</span>
    <span>A</span>
    <div className={'tooltip'}>Tap me!</div>
  </div>
);

const LittleHeart = (props) => <div className="LittleHeart" style={{left: props.positionLeft}}>‚ù§</div>;

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeColor: null,
      activeBackgroundColor: null,
      top: null,
      left: null,
      topSpan: null,
      leftSpan: null,
      initialPositionLittleHeart: null,
      littleHearts: [],
      easings: ['easeInQuad', 'easeOutQuad', 'easeInOutQuad',
        'easeInCubic', 'easeOutCubic', 'easeInOutCubic',
        'easeInQuart', 'easeOutQuart', 'easeInOutQuart',
        'easeInQuint', 'easeOutQuint', 'easeInOutQuint',
        'easeInSine', 'easeOutSine', 'easeInOutSine',
        'easeInExpo', 'easeOutExpo', 'easeInOutExpo',
        'easeInCirc', 'easeOutCirc', 'easeInOutCirc',
        'easeInBack', 'easeOutBack', 'easeInOutBack']
    };
  }

  componentDidMount() {
    this.setState({
      ...this.state,
      littleHearts: new Array(20).fill(undefined),
    });

    this.animeComponents({
      activeColor: '#7EEA6D',
      top: '50%',
      left: '50%',
      activeBackgroundColor: '#BD10E0',
    });

    anime({
      targets: '.tooltip',
      opacity: 1,
      delay: 1000,
      easing: 'easeInOutQuad'
    });

    setTimeout(() => this.setState({
      ...this.state,
      initialPositionLittleHeart: this._generateRandomLeftDirections(),
    }));

    setTimeout(() => this._startLittleHearts(), 300);
  }

  _startLittleHearts() {
    const randomLeftDirection = this._generateRandomLeftDirections();

    for(let i = 0; i < this.state.littleHearts.length + 1; i++) {
      anime({
        targets: `.LittleHeart:nth-child(${i+1})`,
        top: -50,
        left: randomLeftDirection[i],
        duration: 5000,
        loop: true,
        delay: 100,
        easing: this.state.easings[i],
      })
    }
  }

  _generateRandomLeftDirections() {
    return this.state && this.state.littleHearts && this.state.littleHearts.map(() => {
      return Math.floor(Math.random() * 100) + '%';
    });
  }

  randomColor() {
    return COLORS[COLORS.length * Math.random() << 0];
  };

  getColorAndBackgroundColor() {
    const color = this.randomColor();
    const backgroundColor = this.randomColor();
    return color !== backgroundColor
      ? {color, backgroundColor}
      : this.getColorAndBackgroundColor();
  }

  randomPositions() {
    return {
      top: (Math.floor(Math.random() * 60) + 20) + '%',
      left: (Math.floor(Math.random() * 40) + 30) + '%',
    }
  }

  randomPositionsForSpan() {
    return {
      top: (Math.floor(Math.random() * -100) + 50) + '%',
      left: (Math.floor(Math.random() * -100) + 50) + '%',
    }
  }

  update() {
    const randomColors = this.getColorAndBackgroundColor();
    const randomPositions = this.randomPositions();
    const randomPositionsForSpan = this.randomPositionsForSpan();
    this.setState({
      activeColor: randomColors.color,
      activeBackgroundColor: randomColors.backgroundColor,
      top: randomPositions.top,
      left: randomPositions.left,
      topSpan: randomPositionsForSpan.top,
      leftSpan: randomPositionsForSpan.left,
    });

    setTimeout(() => this.animeComponents({
      activeColor: this.state.activeColor,
      top: this.state.top,
      left: this.state.left,
      activeBackgroundColor: this.state.activeBackgroundColor,
      topSpan: this.state.topSpan,
      leftSpan: this.state.leftSpan,
    }));
  }

  animeComponents(options) {
    anime({
      color: options.activeColor,
      top: options.top,
      left: options.left,
      targets: '.Heart',
      easing: 'easeInOutQuint'
    });

    for (let i = 0; i < 19; i++) {
      anime({
        color: options.activeColor,
        top: options.topSpan,
        left: options.leftSpan,
        targets: `.Heart span:nth-child(${i + 1})`,
        easing: this.state.easings[i],
      });
    }

    anime({
      targets: '.App',
      backgroundColor: options.activeBackgroundColor,
      easing: 'easeInOutQuad'
    });
    anime({
      targets: '.tooltip',
      opacity: 0,
      easing: 'easeInOutQuad'
    });
  }

  render() {
    return (
      <div className="App">
        <Heart onClick={() => this.update()}/>
        {this.state.littleHearts.map((little, i) => {
          return <LittleHeart
            key={i}
            positionLeft={this.state.initialPositionLittleHeart && this.state.initialPositionLittleHeart[i]}/>
        })}
      </div>
    );
  }
}


export default App;
